* {
	box-sizing: border-box;
	line-height: 50px;
}
.mainContainer {
	margin-top: 10%;
}
.flexContainer {
	width: 200px;
	margin: auto;
	display: flex;
	flex-direction: row;
}
.button {
	width: 50px;
	text-align: center;
	background-color: lightgray;
	border: 1px solid black;
}
.orange {
	background-color: orange;
}
.darkGray {
	background-color: darkgray;
}
.result {
	border: 1px solid black;
	flex-grow: 1;
	text-align: right;
	background: black;
	color: white;
	padding: 0 7px;
	height: 50px;
	overflow: hidden;
}
.doubleWidth {
	flex: 2;
}
